import React from 'react'
import image from "../img/4888e7antro.jpg"
import Fade from 'react-reveal/Fade';

function Inicio() {
    return (
        <div className="Home" style={{backgroundColor: " #14192f"}}> 
        <Fade up>
        <div className="Home_Letter">
            <h1>Lorem ipsum dolor sit </h1>
            <p >Lorem ipsum dolor sit amet, consectetur adipisicing elit. Enim, quas quo? Eaque quas laudantium, saepe officia fugiat quis doloribus atque quod corporis error, nemo culpa numquam fugit eius, id dicta!</p>
        </div>
            <div className="image_container">
                <img 
                className="home_img" 
                src={image} 
                alt="Hello!" />
            </div>
            </Fade>
        </div>
        
    )
}

export default Inicio
