import React,{useState, useEffect} from 'react'

function ListTodo() {
    const [todo, setTodo] = useState([])

    async function getTodo (){
        const fetched = await fetch("http://localhost:4000/todos")
        const allTodo = await fetched.json()
        setTodo(allTodo)
        
    }
    useEffect(()=>{
        getTodo()
    },[])
    return (
        <div>hola
        {todo.map((e)=> (<div ><p>{e.description}</p></div>))}
        </div>
    )
}

export default ListTodo
