import Divider from '@material-ui/core/Divider';
import Drawer from '@material-ui/core/Drawer';
import Hidden from '@material-ui/core/Hidden';
import IconButton from '@material-ui/core/IconButton';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import MenuIcon from '@material-ui/icons/Menu';
import Typography from '@material-ui/core/Typography';
import AppBar from '@material-ui/core/AppBar'
import CssBaseline from '@material-ui/core/CssBaseline';
import { makeStyles, useTheme } from '@material-ui/core/styles';
import Toolbar  from '@material-ui/core/Toolbar';
import React from "react";
import Home from "@material-ui/icons/Home";
import AddCircle from "@material-ui/icons/AddCircle";
import Waves from "@material-ui/icons/Waves";
import PeopleAlt from "@material-ui/icons/PeopleAlt";
import {BrowserRouter as Router, Route, Switch,Link} from "react-router-dom";
import Inicio from "./Components/Inicio"
import Create from './Components/Create';
import { createMuiTheme, ThemeProvider } from '@material-ui/core';
import logo from "./img/logo.jpg";
import { MoreVert } from '@material-ui/icons';


const drawerWidth = 240;

const thema = createMuiTheme({
  palette:{
    primary:{
      main:"#14192f",},
    secundary:{
      main: "#803c8f"
    }
  }
})

const drawerObjects =[
    {
        name:"Inicio",
        icon:<Home color="secondary"/>,
        path:"/"
    },{
        name:"Beach",
        icon:<Waves color="secondary"/>,
        path:"/beach"},{

        name:"Crear Anuncio",
        icon:<AddCircle color="secondary"/>,
        path:"/create"
      },{
            
        name:"Contact Us",
        icon:<PeopleAlt color="secondary"/>,
        path:"/aboutUs"
        }
]


const useStyles = makeStyles((theme) => ({
  root: {
    display: 'flex',
  },
  drawer: {
    [theme.breakpoints.up('sm')]: {
      width: drawerWidth,
      flexShrink: 0,
    },
  },
  appBar: {
    [theme.breakpoints.up('sm')]: {
     width: `calc(100% - ${drawerWidth}px)`,
      marginLeft: drawerWidth,
    },
  },
  menuButton: {
    marginRight: theme.spacing(1),
    [theme.breakpoints.up('sm')]: {
      display: 'none',
    },
  },
  // necessary for content to be below app bar
  toolbar:theme.mixins.toolbar,
  drawerPaper: {
    width: drawerWidth,
  },
  content: {
    flexGrow: 1,
  },
}));

function App(props) {
  const { window } = props;
  const classes = useStyles();
  const theme = useTheme();
  const [mobileOpen, setMobileOpen] = React.useState(false);
  
  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <div className="drawer" >
      
      <List>
      <img src={logo} style={{height : 36, marginTop:"20px", marginLeft:"40%"}}/>
        {drawerObjects.map((item) => (
          <Link className="links"  to={item.path}>
          <ListItem button key={item.name}>
            <ListItemIcon style={{marginLeft:"20px"}}>{item.icon}</ListItemIcon>
            <p>{item.name}</p>
          </ListItem>
          </Link>
          
        ))}
      </List>
      <Divider />
    </div>
  );

  const container = window !== undefined ? () => window().document.body : undefined;

  return (
    <ThemeProvider theme={thema}>
    <Router>
    <div className={classes.root}>
      <CssBaseline />
      <AppBar position="fixed" className={classes.appBar}>
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            className={classes.menuButton}
          >
            <MoreVert />
          </IconButton>
          <img alt="" src={logo} style={{height : 36, marginRight: 10}}/>
          
          <h1 className="nexus">Nexus</h1>
        </Toolbar>
      </AppBar>
      <nav className={classes.drawer} aria-label="mailbox folders">
        {/* The implementation can be swapped with js to avoid SEO duplication of links. */}
        <Hidden smUp implementation="css">
          <Drawer
            
            container={container}
            variant="temporary"
            anchor={theme.direction === 'rtl' ? 'right' : 'left'}
            open={mobileOpen}
            onClose={handleDrawerToggle}
            classes={{
              paper: classes.drawerPaper,
            }}
            ModalProps={{
              keepMounted: true, // Better open performance on mobile.
            }}
          >
            {drawer}
          </Drawer>
        </Hidden>
        <Hidden xsDown implementation="css">
          <Drawer
            classes={{
              paper: classes.drawerPaper,
            }}
            variant="permanent"
            open
          >
            {drawer}
          </Drawer>
        </Hidden>
      </nav>
      <main className={classes.content}>
        <div className={classes.toolbar} />
        <Switch>
        <Route exact path="/">
            <Inicio/>
          </Route>
          <Route path="/Create">
            <Create/>
          </Route>
          </Switch>
          
      </main>
    </div>
    </Router>
    </ThemeProvider>
  );
}

export default App;
